<p>Yarn是管理你代码的一个包管理器。可以让我们和世界各地的开发者使用和分享Javascript代码。</p>
<p>所有的代码都是通过 <strong>package</strong> (也可以称作 <strong>模块</strong>)来进行分享的。</p>
<p>NPM and YARN 都是解决相同的事情，</p>
